/* tslint:disable:use-life-cycle-interface */
import { $$iterator } from 'rxjs/symbol/iterator';

/**
 * The data type that the Grid expects.
 */
export interface GridDataResult {
    /**
     * The data to be rendered by the Grid as an array.
     */
    data: any[];

    /**
     * The total number of records that is available.
     */
    total: number;
}

/**
 * @hidden
 */
export class DataResultIterator {
    private isObject: boolean;

    constructor(private source: GridDataResult | any[]) {
        this.source = this.source ? this.source : [];

        this.isObject = this.isGridDataResult(this.source);
    }

    protected isGridDataResult(source: GridDataResult | any[]): source is GridDataResult {
        return (<GridDataResult>source).total !== undefined && (<GridDataResult>source).data !== undefined;
    }

    public get total(): number {
        return this.isObject ? (<GridDataResult>this.source).total : (<any[]>this.source).length;
    }

    public get data(): any[] {
        return this.isObject ? (<GridDataResult>this.source).data : (<any[]>this.source);
    }

    public map(fn: (item: any, index: number, array: any[]) => any): any[] {
        return this.data.map(fn);
    }

    public filter(fn: (item: any, index: number, array: any[]) => boolean): any[] {
        return this.data.filter(fn);
    }

    public reduce(fn: (prevValue: any, curValue: any, curIndex: number, array: any[]) => any, init: any): any {
        return this.data.reduce(fn, init);
    }

    public forEach(fn: (item: any, index: number, array: any[]) => void): void {
        this.data.forEach(fn);
    }

    public some(fn: (value: any, index: number, array: any[]) => boolean): boolean {
        return this.data.some(fn);
    }

    public [$$iterator](): any {
        return this.data[$$iterator]();
    }

    public toString(): string { return this.data.toString(); }
}

/**
 * @hidden
 */
export class DataCollection {
    constructor(private accessor: () => DataResultIterator) { }

    public get total(): number { return this.accessor().total; }

    public get length(): number { return this.accessor().data.length; }
    public get first(): any { return this.accessor().data[0]; }
    public get last(): any { return this.accessor().data[this.length - 1]; }

    public map(fn: (item: any, index: number, array: any[]) => any): any[] { return this.accessor().map(fn); }

    public filter(fn: (item: any, index: number, array: any[]) => boolean): any[] {
        return this.accessor().filter(fn);
    }

    public reduce(fn: (prevValue: any, curValue: any, curIndex: number, array: any[]) => any, init: any): any {
        return this.accessor().reduce(fn, init);
    }

    public forEach(fn: (item: any, index: number, array: any[]) => void): void {
        this.accessor().forEach(fn);
    }

    public some(fn: (value: any, index: number, array: any[]) => boolean): boolean {
        return this.accessor().some(fn);
    }

    public [$$iterator](): any {
        return this.accessor()[$$iterator]();
    }

    public toString(): string { return this.accessor().toString(); }
}



